.avatar {
  display: block;
  width: 150px;
  height: 150px;
  text-align: center;
  justify-content: center;
  margin: 10px auto;
}
.name {
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.tag {
  text-align: center;
  color: rgb(88, 86, 86);
}
.location {
  text-align: center;
  color: rgb(88, 86, 86);
}
.profile {
  width: 500px;
  height: 400px;
  justify-content: center;
  margin: 10px auto;
  background-color: aquamarine;
  position: relative;
}
.stats {
  background-color: blueviolet;
  display: flex;
  align-items: flex-end;
  position: absolute;
  bottom: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  justify-content: space-around;
  border-top: fuchsia;
  border-width: 2px;
}
.statsItem {
  width: 100%;
  height: 100%;
  text-align: center;
  border-width: 1;
  border-color: rgb(190, 190, 190);
  border-style: solid;
}
.label {
  display: block;
}
